$font-primary: "Lato", sans-serif;

$fs-xs: 12px;
$lh-xs: 16px;
$fw-normal: 400;
$fw-bold: 700;

$fs-sm: 13px;
$lh-sm: 20px;

$fs-base: 15px;
$lh-base: 20px;

$fs-lg: 18px;
$lh-lg: 24px;

$fs-xl: 24px;
$lh-xl: 32px;

$font-sizes: (
  "12": 12px,
  "13": 13px,
  "15": 15px,
  "18": 18px,
  "24": 24px,
);

$line-heights: (
  "9": 9px,
  "16": 16px,
  "20": 20px,
  "24": 24px,
  "32": 32px,
);

$white: #fff;
$black: #000;

$blue: rgba(64, 126, 201, 1);
$blue-light: rgba(64, 126, 201, 0.08);

$black: rgba(0, 0, 0, 1);

$gray: rgba(91, 104, 136, 1);
$gray-light: rgba(96, 94, 92, 1);
$gray-lighter: rgba(243, 243, 243, 1);

$dark: rgba(32, 31, 30, 1);
$dark-light: rgba(32, 31, 30, 0.04);

$sizes: ();

@for $i from 1 through 100 {
  $sizes: map-merge($sizes, ($i + unquote(""): $i * 1px));
}

$radius: (
  "none": 0px,
  "sm": 2px,
  "base": 4px,
  "md": 6px,
  "lg": 8px,
  "xl": 12px,
  "2xl": 16px,
  "full": 9999px,
);
